import Vue from 'vue';
import Router from 'vue-router';
import axios from 'axios';

// company
const Company = (resolve) => { import('@/components/company/Company').then((module) => { resolve(module); }); };
// product
const ProductList = (resolve) => { import('@/components/product/ProductList').then((module) => { resolve(module); }); };
const ProductDetail = (resolve) => { import('@/components/product/ProductDetail').then((module) => { resolve(module); }); };
const ProductReporHistory = (resolve) => { import('@/components/product/ProductReporHistory').then((module) => { resolve(module); }); };
// news
const NewsList = (resolve) => { import('@/components/news/NewsList').then((module) => { resolve(module); }); };
const NewsDetail = (resolve) => { import('@/components/news/NewsDetail').then((module) => { resolve(module); }); };
// notice
const NoticeList = (resolve) => { import('@/components/notice/NoticeList').then((module) => { resolve(module); }); };
const NoticeDetail = (resolve) => { import('@/components/notice/NoticeDetail').then((module) => { resolve(module); }); };
// vote
const VoteList = (resolve) => { import('@/components/vote/VoteList').then((module) => { resolve(module); }); };
const VoteDetail = (resolve) => { import('@/components/vote/VoteDetail').then((module) => { resolve(module); }); };
const VoteDetailAdmin = (resolve) => { import('@/components/vote/VoteDetailAdmin').then((module) => { resolve(module); }); };
const VoteDetailAdminStatistics = (resolve) => { import('@/components/vote/VoteDetailAdminStatistics').then((module) => { resolve(module); }); };
// survey
const SurveyList = (resolve) => { import('@/components/survey/SurveyList').then((module) => { resolve(module); }); };
const SurveyDetail = (resolve) => { import('@/components/survey/SurveyDetail').then((module) => { resolve(module); }); };
const SurveyDetailAdmin = (resolve) => { import('@/components/survey/SurveyDetailAdmin').then((module) => { resolve(module); }); };

Vue.use(Router);
const userInfo = 'aid/:aid';
// 检查是否登录
function checkLogin(to, from, next, title) {
  let aid = to.params.aid;
  let url = encodeURIComponent(window.location.href);
  axios
    .post('/weixin/user/isLogin')
    .then(res => {
      if (res.data.code === 402) {
        axios
          .post('/weixin/user/login/aid/' + aid + '/callback/' + url)
          .then(res => {
            if (res.data.code === 200) {
              window.location = res.data.data.url;
            }
          })
          .catch(err => {
            console.error(err);
          });
      } else if (res.data.code === 200) {
        next();
      }
    })
    .catch(err => {
      alert(err);
      console.error(err);
    });
  document.title = title;
  // next();
}
export default new Router({
  routes: [
    // 公司
    {
      path: `/company/${userInfo}`,
      name: 'Company',
      component: Company,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next, '公司简介');
      }
    },
    // 产品
    {
      path: `/product/list/${userInfo}`,
      name: 'Product',
      component: ProductList,
      meta: { keepAlive: true },
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next, '产品列表');
      }
    },
    {
      path: `/product/:id/detail/${userInfo}`,
      component: ProductDetail,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next, '产品详情');
      }
    },
    {
      path: `/product/:id/report/history/${userInfo}`,
      component: ProductReporHistory,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next, '投资报告');
      }
    },
    // 新闻
    {
      path: `/news/list/:type/${userInfo}`,
      component: NewsList,
      meta: { keepAlive: true },
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next, '新闻列表');
      }
    },
    {
      path: `/news/:id/detail/:flag/${userInfo}`,
      component: NewsDetail,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next, '新闻详情');
      }
    },
    // 通知
    {
      path: `/notice/list/${userInfo}`,
      component: NoticeList,
      meta: { keepAlive: true },
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next, '通知列表');
      }
    },
    {
      path: `/notice/:id/detail/:flag/:type/${userInfo}`,
      component: NoticeDetail,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next, '通知详请');
      }
    },
    // 投票
    {
      path: `/vote/list/${userInfo}`,
      component: VoteList,
      meta: { keepAlive: true },
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next, '投票列表');
      }
    },
    {
      path: `/vote/:id/detail/${userInfo}`,
      component: VoteDetail,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next, '投票详情');
      }
    },
    {
      path: `/vote/:id/detail/${userInfo}/admin`,
      component: VoteDetailAdmin,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next, '投票');
      }
    },
    {
      path: `/vote/:id/detail/${userInfo}/admin/statistics`,
      component: VoteDetailAdminStatistics,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next, '投票统计');
      }
    },
    // 调研
    {
      path: `/survey/list/${userInfo}`,
      component: SurveyList,
      meta: { keepAlive: true },
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next, '调研列表');
      }
    },
    {
      path: `/survey/:id/detail/${userInfo}`,
      component: SurveyDetail,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next, '调研详情');
      }
    },
    {
      path: `/survey/:id/detail/${userInfo}/admin`,
      component: SurveyDetailAdmin,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next, '调研');
      }
    }
  ]
});



